// [Scope:] Utility functions 

// HTTP Utilities
export const baseURL = "https://aswathraja.com"
export function getData(object) {
    fetch(object.url , {
            method: 'GET', // GET, *POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, *cors, same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            headers: {
                Authorization: object.Authorization,
            },
            data: object.data,
        })
        .catch(err => { 
            if(object.errorCallback) object.errorCallback(err) 
            else console.log(err)
        })
        .then(response => {
            return response.json();
        })
        .catch(err => { 
            console.log(err)
            return [];
        })
        .then(data => { object.successCallback && object.successCallback(data) })
}

export async function postData(object) {
    fetch(object.url , {
            method: 'POST', // GET, *POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, *cors, same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            headers: {
                'Content-Type': 'application/json',
                Authorization: object.Authorization,
                ...object.customHeaders
            },
            body: JSON.stringify(object.data), // body data type must match "Content-Type" header
        })
        .then(response => response.json())
        .catch(err => {if(object.errorCallback) object.errorCallback && object.errorCallback(err)})
        .then(data => {if(object.successCallback) {object.successCallback && object.successCallback(data)}})
        .catch(err => {if(object.errorCallback) object.errorCallback && object.errorCallback(err)})
        .then(data => {if(object.secondSuccessCallback) {object.secondSuccessCallback && object.secondSuccessCallback(data)}})
        .then(data => {return data;})
}

