export function deleteData(object) {
    fetch(object.url , {
        method: 'DELETE', // GET, *POST, PUT, DELETE, etc.
        mode: 'cors', // no-cors, *cors, same-origin
        cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
        headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${object.userToken}`,
        },
        body: JSON.stringify(object.data), // body data type must match "Content-Type" header
    })
    .catch(err => { 
        if(object.errorCallback) object.errorCallback(err) 
        else console.log(err)
    })
    .then(response => response.json())
    .then(data => { object.successCallback && object.successCallback(data) })
}

export function downloadData(object) {
    fetch(object.url , {
            method: 'GET', // GET, *POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, *cors, same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            headers: {
                Authorization: object.Authorization,
                'Access-Control-Allow-Origin':'http://familymediaserver.local',
                ...object.customHeaders
            },
            data: object.data,
        })
        .catch(err => { 
            if(object.errorCallback) object.errorCallback(err) 
            else console.log(err)
        })
        .then(response => {
            return response.blob();
        })
        .catch(err => { 
            console.log(err)
            return [];
        })
        .then(data => { object.successCallback && object.successCallback(data) })
}

