import React, { useEffect } from 'react'

export default function Events(props) {

    const [isVisible, setVisible] = React.useState(false);
    const domRef = React.useRef();
    useEffect(() => {
        const observer = new IntersectionObserver(entries => {
          entries.forEach(entry => setVisible(entry.isIntersecting));
        });
        observer.observe(domRef.current);
        var currentRef = domRef.current;
        return () => observer.unobserve(currentRef);
      }, []);

    return (
        <section className="events section-padding" id="events">
            <div className="container">
                <div className="row">
                    <div className="col-md-12">
                        <h3 className="header">Guide To The Wedding Ceremony</h3>
                    </div>
                </div>
                <div className={`fade-in-section ${isVisible ? 'is-visible' : ''}`} ref={domRef} >
                    <div className="col-md-12 col-sm-12 col-xs-12">
                        <div className="wp3">
                            <p><strong>10<sup>th</sup> December (Saturday) </strong> <span className="time"><strong>5:00PM - 9:30PM</strong></span></p>

                            <h5>Engagement <span className="time"><strong>5PM - 6:30PM</strong></span></h5> 
                            <p className='text-justify'>It's one of the oldest traditional Indian pre wedding ceremony where the bride and groom promise 
                            to marry each other. In India, traditionally, the engagement ceremony involves the exchange of salt and turmeric between the 
                            maternal uncles in the presence of their families. Later, the groom's parents gift a Saree to the bride. The bride and groom 
                            will then exchange rings. This signifies the coming together of the bride and groom and their families. </p>
                        </div>
                        <div className="wp5">
                            <h5>Reception <span className="time"><strong>6:30PM - 9:30PM</strong></span></h5> 
                            <p className='text-justify'>The bride and groom walk down the aisle together and meet and greet the friends and relatives on the stage. It's an evening of music and celebration.</p>
                        </div>
                        <div className="wp6">
                            <h5>Pre Wedding Activities <span className="time"><strong>6:30PM - 9:30PM</strong></span></h5> 
                            <p className='text-justify'>
                            The guests can enjoy the various fun activities organised around the venue including Mehndi stall, Selfie corner, 
                            Portrait Drawing etc. Kids will have fun games like balloons and air castle etc. Dinner will be served throughout the reception timings, please refer to the menu on the brochure given
                            to you or linked below. We would love you to try all the dishes carefully selected to suit all the health needs and tickle 
                            your taste buds.
                            </p>
                        </div>
                    </div>
                    <div className="col-md-12 col-sm-12 col-xs-12">
                        <div className="wp4">
                            <p><strong>11<sup>th</sup> December (Sunday) </strong><span className="time"><strong>6:30AM - 12:30PM</strong></span></p> 
                            <h5>Groom's Procession <span className="time"><strong>6:30AM</strong></span></h5> 
                            <p className='text-justify'>The groom will be accompanied by his family and friends to the wedding venue dressed in traditional Tamil wedding attire.</p>
                        </div>
                        <div className="wp6">
                            <h5>Welcoming the Groom <span className="time"><strong>6:45AM</strong></span></h5>
                            <p className='text-justify'>The bride's family will welcome the groom by doing fireworks and flowers. The Bride's brother will 
                            garland the groom and welcome him followed by the Ladies from the bride's family who will welcome the groom with creatively 
                            and intricately handcrafted Aarthi Thaalis demanding him return gifts to enter the wedding venue.</p>
                        </div>
                        <div className="wp6">
                            <h5>Ganesh Pooja <span className="time"><strong>7:00AM</strong></span></h5>
                            <p>Lord Ganesh is the harbinger of good fortune, prosperity, wisdom and positivity. The Pooja is offered by the groom on the
                                Mandap assisted by a priest. The bride's and groom's families pray for the wellbeing of the couple.
                            </p>
                        </div>
                        <div className="wp6">
                            <h5>Praying the Parents' feet <span className="time"><strong>7:30AM</strong></span></h5> 
                            <p className='text-justify'>The bride and groom will wash the feet and perform a Pooja for their parents to seek the blessings as they start a new life.</p>
                        </div>
                        <div className="wp6">
                            <h5>Presenting Dresses to the Bride and Groom <span className="time"><strong>7:45AM</strong></span></h5>
                            <p className='text-justify'>The groom's parents will present the wedding saree to the bride, and the bride's parents will present an attire to 
                            the groom. The bride will change into the wedding saree and arrive to the Mandap.</p>
                        </div>
                        <div className="wp6">
                            <h5>Manglayatharanam - Tieing the sacred Mangalsutra <span className="time"><strong>8:35AM</strong></span></h5>
                            <p className='text-justify'>This is the main event of the marriage. The groom will tie the auspicious Mangalsutra (Thali) 
                            around the bride's neck and then apply Sindhoor (Kumkum) on her forehead signifying that they are now husband and wife.
                            </p>
                        </div>
                        <div className="wp6">
                            <h5>Exchanging Garlands <span className="time"><strong>8:40AM</strong></span></h5>
                            <p className='text-justify'>Maalai Maatral or exchanging of garlands is a ritual where the bride and groom exchange flower 
                            garlands around their neck to begin a holy union and as a sign of approval from all those who are present for the wedding.</p>
                        </div>
                        <div className="wp6">
                            <h5>Going Around the Agni (Fire Pit) <span className="time"><strong>8:50AM</strong></span></h5> 
                            <p className='text-justify'>The priest ties the ends of the groom's Dhoti and bride's saree and ask them to walk around the 
                            fire pit thrice while holding hands and chanting shlokas. This signifies the vows taken by the bride and groom in the presence
                            of the fire gods.</p>
                        </div>
                        <div className="wp6">
                            <h5>Sapthapadhi <span className="time"><strong>8:55AM</strong></span></h5> 
                            <p className='text-justify'>The word Sapthapadhi means 'Seven Steps' in sanskrit. The newly-wed couple take seven steps 
                            together with each step signifying a promise they make to each other.</p>
                        </div>
                        <div className="wp6">
                            <h5>Metti, Ammi &amp; Arunthathi Parthal <span className="time"><strong>9:00AM</strong></span></h5>
                            <p className='text-justify'>The groom will keep the bride's feet on the Ammi (stone) and put a metti ring (toe ring) on the 
                            bride's toe and show the stars - Arunthathi. This is to signify that the bride and groom shall always be with a strong 
                            personality in life like the stone and together for this lifetime like Arunthathi. Arunthathi which is one of the stars 
                            in the Saptarishi constellation of stars is believed to always be together with her husband Vashisthar - another star in the
                            same constellation This is to signify the bride and groom shall always be together just like them throughout their lives and
                            the next 7 generations to come.
                            </p>
                        </div>
                        <div className="wp6">
                            <h5>Post Wedding Rituals <span className="time"><strong>9:30AM</strong></span></h5> 
                            <p className='text-justify'>
                            The newly-weds will get blessings from their families and friends. The bride and groom will play some small traditional games 
                            after their wedding rituals.
                            </p>
                        </div>
                        <div className="wp6">
                            <h5>Fun Wedding Activities <span className="time"><strong>6:30AM - 9:30AM</strong></span></h5> 
                            <p className='text-justify'>
                            The bride and groom with personally thank everyone and take candid photos with the guests after the wedding. 
                            Other activities for the guests include prop photo booth, bangles stall. Enjoy all of these with some snacks and drinks from 
                            the various stalls around the venue.
                            </p>
                        </div>
                        <div className="wp6">
                            <h5>Lunch <span className="time"><strong>12:00PM - 2:00PM</strong></span></h5> 
                            <p className='text-justify'>
                            Lunch will be served from 12:00PM onwards. Please find the menu in the brochure given to you or linked below, we would love you 
                            to try all the dishes carefully selected to suit all the health needs and tickle your taste buds.
                            </p>
                        </div>
                    </div>
                </div>
                {/* <div className="row section-padding">
                    <div className="col-md-4 offset-md-4 text-center col-sm-6">
                        <button className="btn btn-accent btn-small" disabled>
                        <i className="fa-regular fa-rectangle-list p-r-5"></i>Download Event Brochure & Food Menu
                        </button>
                    </div>
                </div> */}
                {/* <!--<div className="row">
                    <div className="col-xs-12 section-border section-padding">
                    </div>
                </div>--> */}
            </div>
        </section>
    )
}