import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import './App.css';
import BaseTemplate from './components/BaseTemplate/BaseTemplate';

function App() {
  return (
    <Router>
    <Routes>
      <Route exact path="/" element={<BaseTemplate path="/"></BaseTemplate>}/>
      <Route path="*" element={<BaseTemplate path="/"></BaseTemplate>}/>
    </Routes>
  </Router>
  );
}

export default App;
