import React, { useEffect } from 'react'
import { Link } from "react-scroll";
import '../../assets/css/bootstrap.min.css'
import '../../assets/css/bootstrap.min.css.map'
import '../../assets/css/flexslider.css'
import '../../assets/css/ie.css'
import '../../assets/css/jquery.fancybox.css'
import '../../assets/css/normalize.min.css'
import '../../assets/css/print.css'
import '../../assets/css/styles.min.css'
import '../../assets/css/font-awesome/css/all.css'
import '../../assets/css/queries.css'
import logoLg from '../../assets/img/logo-lg.png';
import logo from '../../assets/img/logo.png';
import heroLogo from '../../assets/img/Logo Small.png';

import Invitation from '../Invitation/Invitation'
import Events from '../Events/Events';
import Instagram from '../Instagram/Instagram';
import Venue from '../Venue/Venue';
import Route from '../Route/Route';
import RSVP from '../RSVP/RSVP';
import Footer from '../Footer/Footer';

export default function BaseTemplate(props) {

    const [isScrolled, setScrolled] = React.useState(false);
    const [showHeaderNav,setShowHeaderNav] = React.useState(false);

    const toggleHeaderNav = function() {
        setShowHeaderNav(!showHeaderNav);
    }
    useEffect(() => {
        if(window.innerWidth < 1000)
        {
            setScrolled(true)
        }
        var scrollEventListener = window.addEventListener("scroll", () => {
            if(window.innerWidth < 1000)
            {
                setScrolled(true)
            }
            else
            {
                if(window.scrollY > 40)
                {
                    setScrolled(true)
                }
                else
                {
                    setScrolled(false)
                }
            }
        }) 
        return () => window.removeEventListener("scroll",scrollEventListener)
      }, []);

    const hideHeaderNav = function() {
        setShowHeaderNav(false);
    }

    return (
        <div>
            <section id="hero" className="hero">
    <section className={`navigation ${isScrolled ? 'fixed' : ''}`}>
        <header className={`${isScrolled ? 'p-35' : ''}`}>
            <div className="header-content">
                <div className="logo"><a href="/"><img src={logo} alt="A❤️B"/></a></div>
                <div className={showHeaderNav ? "header-nav open" : "header-nav"}>
                    <nav>
                        <ul className="primary-nav">
                            <li><Link
                            to="invitation"
                            spy={true}
                            smooth={true}
                            onClick={hideHeaderNav}
                            offset={-85}
                            duration={100} className="text-white text-decoration-none">
                                Invitation
                            </Link></li>
                            <li><Link
                            to="events"
                            onClick={hideHeaderNav}
                            spy={true}
                            smooth={true}
                            offset={-50}
                            duration={100} className="text-white text-decoration-none">
                                Events
                            </Link></li>
                            <li><Link
                            onClick={hideHeaderNav}
                            to="instagram"
                            spy={true}
                            smooth={true}
                            offset={-50}
                            duration={100} className="text-white text-decoration-none">
                                Instagram
                            </Link></li>
                            {/* <li><a href="#eng-pics">Engagement</a></li> */}
                            <li className="text-white text-decoration-none">
                            <Link
                            onClick={hideHeaderNav}
                            to="instagram"
                            spy={true}
                            smooth={true}
                            offset={175}
                            duration={100}>
                                Glimpse of the venue
                            </Link>
                            </li>
                            <li className='d-md-block d-lg-none'><Link
                            to="map"
                            spy={true}
                            smooth={true}
                            offset={-300}
                            duration={100}
                            onClick={hideHeaderNav}
                            className="login text-decoration-none">Venue</Link></li>
                            <li className='d-md-block d-lg-none'><Link
                            to="rsvp"
                            spy={true}
                            smooth={true}
                            offset={-100}
                            onClick={hideHeaderNav}
                            duration={100}><button className="btn btn-accent transperant-bg btn-small button-accent-color">RSVP</button>
                        </Link></li>
                        </ul>
                        <ul className={`member-actions d-md-none d-lg-block ${isScrolled ? 't-25' : 't-35'}`}>
                            <li><Link
                            to="map"
                            spy={true}
                            smooth={true}
                            offset={-300}
                            duration={100}
                            className="login text-decoration-none">Venue</Link></li>
                            <li><Link
                            to="rsvp"
                            spy={true}
                            smooth={true}
                            offset={-100}
                            duration={100}><button className="btn btn-accent transperant-bg btn-small button-accent-color text-decoration-none">RSVP</button>
                        </Link></li>
                        </ul>
                    </nav>
                </div>
                <div className="navicon">
                    <span className={showHeaderNav ? "nav-toggle active" : "nav-toggle"} onClick={toggleHeaderNav}><span></span></span>
                </div>
            </div>
        </header>
    </section>
    <div className="container">
        <div className="row">
            <div className="d-flex justify-content-center">
                <div className="hero-content text-center">
                    {/* <!--<h1>Ram and Antara</h1>--> */}
                    <div className='d-flex justify-content-center'><div className='container'><img src={heroLogo} className="hero-logo" alt="A❤️B"/> </div></div>
                    <div><img src={logoLg} alt="Aswath ❤️ Bathmeshwari"/></div>
                   
                    {/* <!--<p className="intro">Ram & Antara are getting hitched!</p>--> */}
                    {/* <!--<a href="#" className="btn btn-fill btn-large btn-margin-right">Scroll Down</a> --> */}
                </div>
            </div>
        </div>
        <div className="row">
            <div className="col-sm-4 hidden-xs"></div>
            <div className="col-sm-4 hidden-xs text-center">
            `   <Link
                to="rsvp"
                spy={true}
                smooth={true}
                offset={-100}
                duration={500}>
                    <button className="btn btn-accent btn-large transperant-bg rsvp-btn button-accent-color">RSVP</button>
                </Link>
            </div>
            <div className="col-sm-4 hidden-xs"></div>
        </div>
    </div>
    <div className="down-arrow floating-arrow"><Link
      to="invitation"
     spy={true}
     smooth={true}
     offset={-100}
     duration={500}>
        <i className="fa fa-angle-down"></i>
     </Link></div>
</section>
        <Invitation/>
        <Events/>
        <Instagram/>
        <Venue/>
        <Route/>
        <RSVP/>
        <Footer/>
        </div>
    )
}