import React, { useState, useRef, useEffect } from 'react'
import Segmented from 'rc-segmented';
import 'rc-segmented/assets/index.css'; // import 'rc-segmented/asssets/index.less';
import '../../assets/css/bootstrap.min.css'
import '../../assets/css/flexslider.css'
import '../../assets/css/ie.css'
import '../../assets/css/jquery.fancybox.css'
import '../../assets/css/normalize.min.css'
import '../../assets/css/print.css'
import '../../assets/css/queries.css'
import '../../assets/css/styles.min.css'
import { postData, baseURL } from '../../utils/utils'
import toast, { Toaster } from 'react-hot-toast';



export default function RSVP(props) {

    var rsvpFormRef = useRef("rsvpForm");
    var phoneNumberFieldRef = useRef("phoneNumberField");
    var emailIdFieldRef = useRef("emailIdField");
    var [phoneNumber,setPhoneNumber] = useState("");
    var [name,setName] = useState("");
    var [email,setEmail] = useState("");
    var [extras,setExtras] = useState('');
    var [inviteCode,setInviteCode] = useState('');
    var [guesttype,setGuestType] = useState("Groom's Guest");
    var [altphone,setAltPhone] = useState("");
    var [rsvp,setRSVP] = useState({});
    var [personalrequests,setPersonalRequests] = useState('');
    var [savedPhoneNumber,setSavedPhoneNumber] = useState("");
    var [savedEmail,setSavedEmail] = useState('');
    var [showRetrieveBooking,setShowRetrieveBooking] = useState(false);
    var [showUpdateBooking,setShowUpdateBooking] = useState(false);
    const saveRSVP = async function(event) {
        try{
                event.preventDefault();
                var altPhoneString = typeof altphone === 'string'? "," + altphone.replaceAll(new RegExp(/(\r\n|\r|\n)/g),",") + "," : ""
                altPhoneString = altPhoneString.replaceAll(new RegExp(/(?:\r\n|\r|\n)/g),"")
                let rsvp = {
                    phone: phoneNumber,
                    name: name,
                    guests: extras,
                    email: email,
                    personalrequests: personalrequests,
                    altphone: altPhoneString,
                    guesttype:guesttype
                }
                setSavedEmail(email);
                setSavedPhoneNumber(phoneNumber);
                setPhoneNumber("");
                setEmail("");
                setName("");
                setExtras("");
                setInviteCode("");
                setPersonalRequests("");
            
                await postData({
                    url: baseURL + `/api/rsvp/saversvp`,
                    successCallback: res => {
                        if (res)
                        {
                            setShowRetrieveBooking(false);
                            setShowUpdateBooking(false);
                            setPhoneNumber("");
                            setEmail("");
                            setName("");
                            setExtras("");
                            setInviteCode("");
                            setPersonalRequests("");
                            setAltPhone("");
                            setRSVP({});
                            setGuestType("Groom's Guest");
                            return 'Saved your request, Thank you for coming!'
                        }
                        else
                        {
                            throw new Error("Error Saving the Request")
                        }
                },
                Authorization: '',
                data:rsvp
            })
        }
        catch(e)
        {
            throw new Error('Your request did not go through. Please contact the administrator');
        }
    }

    const disableScroll = function(event) {
        event.target.blur()
    }

    const retrieveRSVP = async function(event) {
        try{
                event.preventDefault();
                switch(event.target.id)
                {
                    case "email":
                        setEmail(event.target.value)
                        break;
                    case "phone":
                        setPhoneNumber(event.target.value);
                        break;
                    case "name":
                        setName(event.target.value);
                        break;
                    case "extras":
                        setExtras(event.target.value);
                        break;
                    case "altphone":
                        var multiphoneRegEx = new RegExp('([0-9])+','gm')
                        if((event.target.value.length > 0 && multiphoneRegEx.test(event.target.value)) | (event.target.value.length === 0))
                        {
                            var value = event.target.value
                            value = value.toString().replaceAll(/[a-zA-Z]/igm, "")
                            value = value.replace(new RegExp("\\!|\\@|\\#|\\$|\\^|\\&|\\%|\\*|\\|\\(|\\)|\\+|\\=|\\[|\\]|\\|\\/|\\{|\\}|\\||\\:|\\;|\\<|\\>|\\?|\\`|\\~", "gi"), "");
                            value = value.replace(new RegExp("\\-\\-|\\-\\,|\\-\\ |\\-\\.|\\ \\-", "gi"), "-");
                            value = value.replace(new RegExp("\\,\\,|\\,\\-|\\,\\ |\\,\\.|\\ \\,", "gi"), ",");
                            value = value.replace(new RegExp("\\.\\.|\\.\\-|\\.\\ |\\.\\,|\\ \\.", "gi"), ".");
                            value = value.replace(new RegExp("\\.\\n|\\-\\n|\\,\\n |\\ \\n", "gi"), "\n");
                            setAltPhone(value);
                        }
                        break;
                    case "personalrequests":
                        setPersonalRequests(event.target.value);
                        break;
                    case "invitecode":
                        setInviteCode(event.target.value);
                        break;
                    default:
                        break;
                }
            }
            catch(e)
            {
                throw new Error(e);
            }
    }

    const populateRSVP = function(event) {
        try{
            event.preventDefault();
            if(parseInt(inviteCode) === rsvp.invitecode)
            {
                setGuestType(rsvp.guesttype.toString());
                setShowRetrieveBooking(false);
                setShowUpdateBooking(true);
            }
            else
            {
                toast.error("Your invite code is incorrect. Please enter the invite code : " + rsvp.invitecode, {duration:10000} )
                setShowUpdateBooking(false);
                setShowRetrieveBooking(true);
            }
        }
        catch(e)
        {
            toast.error("Your invite code is incorrect. Please enter the invite code : " + rsvp.invitecode, {duration:10000} )
        }
    }

    const displayToast = async function(event) {
        event.preventDefault();
        if(!showRetrieveBooking || (showUpdateBooking && event.target.id === "RetrievedConfirmationBtn") || event.target.id === "SaveConfirmationBtn") 
        {
            toast.promise(saveRSVP(event),
            {
                loading:'Saving your request..',
                success:<b>Saved your request, Thank you for coming!</b>,
                error:<b>Your request did not go through. Please contact the admin</b>,
            },)
        }
        else
        {
            toast.success("Thank you for coming, here are your reservations");
        }
    }

    useEffect(() => {
        function VerifyData() {
            let verifyData = {
                email:email ? email : "",
                phone:phoneNumber ? phoneNumber : ""
            }
            if(((email && email.length > 0 && email.length !== savedEmail.length && email.toString() !== savedEmail.toString()) || (phoneNumber && phoneNumber.length > 0 && phoneNumber.toString() !== savedPhoneNumber.toString() && savedPhoneNumber.length !== phoneNumber.length)))
            {
                postData({
                    url: baseURL +`/api/rsvp/retrieversvp`,
                    successCallback: res => {
                        if (res.newRSVP === false)
                        {
                            if(rsvp.id !== res.rsvp.id)
                            {
                                setRSVP(res.rsvp);
                                toast.success("Enter the following invite code to retrieve your RSVP : " + res.rsvp.invitecode ,{duration:10000});
                                setShowRetrieveBooking(true);
                            }
                        }
                        else
                        {
                            setRSVP({});
                            setShowRetrieveBooking(false);
                        }
                    },
                    errorCallback: err => {
                        throw new Error(err)
                    },
                    Authorization: '',
                    data:verifyData
                })
            }
            else {
                setTimeout(() => {
                    setSavedEmail("");
                    setSavedPhoneNumber("");
                }, 2500);
            }
        }
        var Timer = setTimeout(() => {
            VerifyData();
        },500)
        return () => {
            clearTimeout(Timer)
        }
    },[email,phoneNumber,savedEmail,savedPhoneNumber,rsvp.id])

    useEffect(() => {
        if(showUpdateBooking)
        {
            var altPhoneString = rsvp.altphone.substring(1,rsvp.altphone.length-1).replaceAll(",","\n")
            setInviteCode("");
            setEmail(rsvp.email);
            setName(rsvp.name);
            setPhoneNumber(rsvp.phone);
            setExtras(rsvp.guests);
            setPersonalRequests(rsvp.personalrequests);
            setAltPhone(altPhoneString)
        }
        else
        {
            if(rsvp.phone === savedPhoneNumber)
            {
                setEmail("")
                setName("")
                setPhoneNumber("")
                setExtras("")
                setPersonalRequests("")
                setAltPhone("")
            }
        }
    },[showUpdateBooking,rsvp,savedPhoneNumber])

    if(showRetrieveBooking)
    {
        return (
            <section className="rsvp text-center" id="rsvp">
                <Toaster 
                  position="top-center"
                />
                
                <div className="container">
                    <div className="d-flex justify-content-center">
                        <div className="col-sm-12 col-xs-12 col-md-9">
                            <h3>RSVP Here</h3>
                            <p>We would greatly appreciate if you could RSVP at the earliest</p>
                            <form id="rsvp-form" className="rsvp-form" ref={rsvpFormRef} onSubmit={populateRSVP}>
                                <div className="row">
                                    <div className="offset-md-1 col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-phone"></i>
                                            <input type="tel" name="phone" id="phone"
                                                className="" min="0"
                                                placeholder="Phone" pattern="[+]*([0-9]{2})*[-]*[0-9]{5}[-]*[0-9]{5}" onChange={retrieveRSVP}
                                                onBlur={retrieveRSVP} autoComplete="off" autoCorrect={undefined} value={phoneNumber}
                                                ref={phoneNumberFieldRef}
                                                />
                                        </div>
                                    </div>
                                    <div className="col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-user"></i>
                                            <input name="name" id="name" className="" autoComplete="off" autoCorrect={undefined} onChange={retrieveRSVP} 
                                            placeholder="Your name" value={name} 
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="offset-md-1 col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-users"></i><input type="number" name="extras" id="extras" className="" min="0"
                                                                            max="10"
                                                                            placeholder="Husband/Wife or kids" autoComplete="off" autoCorrect={undefined} value={extras} onChange={retrieveRSVP} onWheel={disableScroll}
                                                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-envelope"></i>
                                            <input type="email" name="email" id="email" className="" autoComplete="off" autoCorrect={undefined}
                                                placeholder="Your email" onChange={retrieveRSVP} value={email} ref={emailIdFieldRef}
                                                />
                                        </div>
                                    </div>
                                    <div className="offset-md-1 col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-phone"></i>
                                            <textarea name="altphone" id="altphone"
                                                className="" min="0"
                                                placeholder="Alternate Phone Numbers" autoComplete="off" 
                                                autoCorrect={undefined} onBlur={retrieveRSVP} onChange={retrieveRSVP} value={altphone}
                                                />
                                        </div>
                                    </div>
                                    <div className="col-md-5 col-sm-12">
                                        <div className="form-input-group">
                                        <Segmented
                                            options={["Groom's Guest", "Bride's Guest"]}
                                            onChange={(value) => {setGuestType(value.toString());}}
                                            className="font-weight-400"
                                            defaultValue={"Groom's Guest"}
                                            value={guesttype}
                                        />
                                        </div>
                                    </div>
                                    <div className="offset-md-1 col-md-10 col-sm-12">
                                        <div className="form-input-group">
                                            <i className="fa fa-comments"></i>
                                            <textarea name="personalrequests" id="personalrequests"
                                                className="personalrequests" min="0" value={personalrequests} onChange={retrieveRSVP}
                                                placeholder="Any personal requests or help needed during the wedding dates"
                                                />
                                        </div>
                                    </div>
                                    <div className="offset-md-1 col-md-10 col-sm-12">
                                        <div className="form-input-group">
                                            
                                            <i className="fa fa-key"></i><input type="number" name="invitecode" id= "invitecode" className="" min="1000" 
                                                                            max="9999"
                                                                            placeholder="Invite Code" value={inviteCode} onChange={retrieveRSVP} onWheel={disableScroll}
                                                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className='row'>
                                    <div className='offset-md-1 offset-lg-1 offset-xl-1 col-sm-12 col-xs-12 col-lg-10 col-md-10 col-xl-10'>
                                        <button className="btn-fill rsvp-btn">
                                            Retrieve
                                        </button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
    else if(showUpdateBooking)
    {
        return (
            <section className="rsvp text-center" id="rsvp">
                <Toaster 
                position="bottom-center"
                />
                <div className="container">
                    <div className="d-flex justify-content-center">
                        <div className="col-sm-12 col-xs-12 col-md-9">
                            <h3>RSVP Here</h3>
                            <p>We would greatly appreciate if you could RSVP at the earliest</p>
                            <form id="rsvp-form" className="rsvp-form" ref={rsvpFormRef} onSubmit={displayToast}>
                                <div className="row">
                                    <div className="offset-md-1 col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-phone"></i>
                                            <input type="tel" name="phone" id="phone"
                                                className="" min="0"
                                                placeholder="Phone" pattern="[+]*([0-9]{2})*[-]*[0-9]{5}[-]*[0-9]{5}" autoComplete="off" 
                                                autoCorrect={undefined} onBlur={retrieveRSVP} onChange={retrieveRSVP} value={phoneNumber} ref={phoneNumberFieldRef}
                                                required/>
                                        </div>
                                    </div>
                                    <div className="col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-user"></i>
                                            <input name="name" id="name" className=""
                                            placeholder="Your name" onChange={retrieveRSVP} value={name}
                                            required/>
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="offset-md-1 col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-users"></i><input type="number" name="extras" id= "extras" className="" min="0"
                                                                            max="10"
                                                                            placeholder="Husband/Wife or kids" value={extras} onChange={retrieveRSVP} onWheel={disableScroll} 
                                                                            required/>
                                        </div>
                                    </div>
                                    <div className="col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-envelope"></i>
                                            <input type="email" name="email" id="email" className=""
                                                placeholder="Your email" onChange={retrieveRSVP} value={email}
                                                autoComplete="off" autoCorrect={undefined} ref={emailIdFieldRef}
                                                required/>
                                        </div>
                                    </div>
                                    <div className="offset-md-1 col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-phone"></i>
                                            <textarea name="altphone" id="altphone"
                                                className="" min="0"
                                                placeholder="Alternate Phone Numbers" autoComplete="off" 
                                                autoCorrect={undefined} onBlur={retrieveRSVP} onChange={retrieveRSVP} value={altphone}
                                                />
                                        </div>
                                    </div>
                                    <div className="col-md-5 col-sm-12">
                                        <div className="form-input-group">
                                        <Segmented
                                            options={["Groom's Guest", "Bride's Guest"]}
                                            onChange={(value) => {setGuestType(value.toString());}}
                                            className="font-weight-400"
                                            defaultValue={"Groom's Guest"}
                                            value={guesttype}
                                        />
                                        </div>
                                    </div>
                                    <div className="offset-md-1 col-md-10 col-sm-12">
                                        <div className="form-input-group">
                                            <i className="fa fa-comments"></i>
                                            <textarea name="personalrequests" id="personalrequests"
                                                className="personalrequests" min="0" value={personalrequests} onChange={retrieveRSVP}
                                                placeholder="Any personal requests or help needed during the wedding dates"
                                                required/>
                                        </div>
                                    </div> 
                                </div>
                                <div className='row'>
                                    <div className='offset-md-1 offset-lg-1 offset-xl-1 col-sm-12 col-xs-12 col-lg-10 col-md-10 col-xl-10'>
                                        <button className="btn-fill rsvp-btn" id="RetrievedConfirmationBtn" type="submit">
                                            Confirm
                                        </button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                    <br/>
                    <h3>Your Reservations</h3>
                    <table className='table'>
                        <thead>
                        <tr className='reservation-table-widths'>
                                <th>
                                    Hotel Name
                                </th>
                                <th>
                                    Hotel Address
                                </th>
                                <th>
                                    Hotel Contact
                                </th>
                                <th>
                                    Map
                                </th>
                                <th>
                                    Transport Type
                                </th>
                                <th>
                                    Transport Phone
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {rsvp.reservations && rsvp.reservations.length > 0 ? rsvp.reservations.map((reservation,index) => {
                            return(
                            <tr key={index}>
                                <td>{reservation.hotelname}</td>
                                <td>{reservation.hoteladdress}</td>
                                <td><a href={"tel://" + reservation.hotelphone}>{reservation.hotelphone}</a></td>
                                <td><a href={reservation.hotelmaplink.substring(1,reservation.hotelmaplink.length -1)}>View Map</a></td>
                                <td>{reservation.transporttype}</td>
                                <td><a href={"tel://" + reservation.transportphone}>{reservation.transportphone}</a></td>

                            </tr>)}) : <tr><td>NA</td> <td>Contact us if not availble by the wedding dates</td><td></td><td></td><td></td><td></td><td></td></tr>}
                        </tbody>
                        <tfoot>
                            <tr>
                                <th>
                                    Hotel Name
                                </th>
                                <th>
                                    Hotel Address
                                </th>
                                <th>
                                    Hotel Contact
                                </th>
                                <th>
                                    Map
                                </th>
                                <th>
                                    Transport Type
                                </th>
                                <th>
                                    Transport Phone
                                </th>
                            </tr>
                        </tfoot>
                    </table>
                    <br/>
                    <h3>Known Guests</h3>
                    <table className='table'>
                        <thead>
                        <tr className='known-guests-table-widths'>
                                <th>
                                    Guest Name
                                </th>
                                <th>
                                    Guest Contact
                                </th>
                                <th>
                                    Hotel Address
                                </th>
                                <th>
                                    Map
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {rsvp.knownrsvps && rsvp.knownrsvps.length > 0 ? rsvp.knownrsvps.map((knownrsvps,index) => {
                            return(
                            <tr key={index}>
                                <td>{knownrsvps.rsvp.name}</td>
                                <td><a href={"tel://" + knownrsvps.rsvp.phone}>{knownrsvps.rsvp.phone}</a></td>
                                <td>{knownrsvps.rsvp.reservations.length>0? knownrsvps.rsvp.reservations.hoteladdress : ""}</td>
                                <td><a href={knownrsvps.rsvp.reservations[0]? knownrsvps.rsvp.reservations[0].hotelmaplink.substring(1,knownrsvps.rsvp.reservations[0].hotelmaplink.length -1) : ""}>{knownrsvps.rsvp.reservations[0]? "View Map" : "" }</a></td>

                            </tr>)}) : <tr><td></td> <td></td><td></td><td></td></tr>}
                        </tbody>
                        <tfoot>
                            <tr>
                                <th>
                                    Guest Name
                                </th>
                                <th>
                                    Guest Contact
                                </th>
                                <th>
                                    Hotel Address
                                </th>
                                <th>
                                    Map
                                </th>
                            </tr>
                        </tfoot>
                    </table>
                </div>
            </section>
        )
    }
    else
    {
        return (
            <section className="rsvp text-center" id="rsvp">
                <Toaster 
                position="bottom-center"
                />
                <div className="container">
                    <div className="d-flex justify-content-center">
                        <div className="col-sm-12 col-xs-12 col-md-9">
                            <h3>RSVP Here</h3>
                            <p>We would greatly appreciate if you could RSVP at the earliest</p>
                            <form id="rsvp-form" className="rsvp-form" ref={rsvpFormRef} onSubmit={displayToast}>
                                <div className="row">
                                    <div className="offset-md-1 col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-phone"></i>
                                            <input type="tel" name="phone" id="phone" pattern="[+]*([0-9]{2})*[-]*[0-9]{5}[-]*[0-9]{5}"
                                                className="" min="0" autoComplete="off" autoCorrect={undefined}
                                                placeholder="Phone" onChange={retrieveRSVP} onBlur={retrieveRSVP}
                                                value={phoneNumber} ref={phoneNumberFieldRef}
                                                required/>
                                        </div>
                                    </div>
                                    <div className="col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-user"></i>
                                            <input name="name" id="name" className=""
                                            placeholder="Your name" onChange={retrieveRSVP} value={name}
                                            required/>
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="offset-md-1 col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-users"></i>
                                            <input type="number" name="extras" id= "extras" className="" min="0"
                                                max="10"
                                                placeholder="Husband/Wife or kids" value={extras} onChange={retrieveRSVP} onWheel={disableScroll}
                                                required/>
                                        </div>
                                    </div>
                                    <div className="col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-envelope"></i>
                                            <input type="email" name="email" id="email" className=""
                                                autoComplete="off" autoCorrect={undefined}
                                                placeholder="Your email" onChange={retrieveRSVP} value={email}
                                                ref={emailIdFieldRef}
                                                />
                                        </div>
                                    </div>
                                    <div className="offset-md-1 col-md-5 col-sm-6">
                                        <div className="form-input-group">
                                            <i className="fa fa-phone"></i>
                                            <textarea name="altphone" id="altphone"
                                                className="" min="0"
                                                placeholder="Alternate Phone Numbers" autoComplete="off" 
                                                autoCorrect={undefined} onBlur={retrieveRSVP} onChange={retrieveRSVP} value={altphone}
                                                />
                                        </div>
                                    </div>
                                    <div className="col-md-5 col-sm-12">
                                        <div className="form-input-group">
                                        <Segmented
                                            id={"guesttype"}
                                            options={["Groom's Guest", "Bride's Guest"]}
                                            onChange={(value) => {setGuestType(value.toString());}}
                                            className="font-weight-400"
                                            defaultValue={guesttype}
                                            value={guesttype}
                                        />
                                        </div>
                                    </div>
                                    <div className="offset-md-1 col-md-10 col-sm-12">
                                        <div className="form-input-group">
                                            <i className="fa fa-comments"></i>
                                            <textarea name="personalrequests" id="personalrequests"
                                                className="personalrequests" min="0" value={personalrequests} onChange={retrieveRSVP}
                                                placeholder="Any personal requests or help needed during the wedding dates"
                                                required/>
                                        </div>
                                    </div>
                                </div>
                                <div className='row'>
                                    <div className='offset-md-1 offset-lg-1 offset-xl-1 col-sm-12 col-xs-12 col-lg-10 col-md-10 col-xl-10'>
                                        <button className="btn-fill rsvp-btn" id="SaveConfirmationBtn" type="submit">
                                            Confirm
                                        </button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}